import React from "react";
import { Link, StaticQuery, graphql } from "gatsby"
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import NavbarBrand from 'react-bootstrap/NavbarBrand'
import NavItem from 'react-bootstrap/NavItem'
import NavLink from 'react-bootstrap/NavLink'
import Container from 'react-bootstrap/Container'
import NavbarToggle from 'react-bootstrap/NavbarToggle'
import NavbarCollapse from 'react-bootstrap/NavbarCollapse'
import NavDropdown from 'react-bootstrap/NavDropdown'

const Navigation = () => (
  <StaticQuery
    query={graphql`
      query {
        strapiGlobal {
          siteName
        }
        allStrapiCategory {
          edges {
            node {
              slug
              name
            }
          }
        }
      }
    `}
    render={(data) => (
        <Navbar fixed="top" expand="lg" variant="dark" bg="dark">
          <Container>
            <NavbarBrand as={Link} to="/">{data.strapiGlobal.siteName}</NavbarBrand>
            <NavbarToggle type="button" label="" aria-controls="navbarResponsive"></NavbarToggle>
            <NavbarCollapse  id="navbarResponsive">
              <Nav className="ml-auto">
                
                <NavDropdown title="" id="nav-dropdown-category">
                  <NavDropdown.Item as={Link} to="/"></NavDropdown.Item>
                  {
                    data.allStrapiCategory.edges.map( e=>{
                      return (
                      <NavDropdown.Item as={Link} to={`/category/${e.node.slug}`}>{`${e.node.name}`}</NavDropdown.Item>
                      )
                    })
                  }
                </NavDropdown>

                
                <NavItem>
                  <NavLink as={Link} to="/"></NavLink>
                </NavItem>
                
              </Nav>
            </NavbarCollapse>
          </Container>
        </Navbar>
    )}
  />
);

export default Navigation;